import React from "react";
import { inject, observer } from "mobx-react";
import Components from "../../components";
import PageStore from "../../stores/PageStore";
const {
  LAYOUT: { NavigationLayout },
  VIEW: { ShipType3PositionView, ShipType5PositionView, ShipType9PositionView },
} = Components;

export default inject(
  "PageStore",
  "APIStore",
  "ModalStore"
)(
  observer(
    class extends React.Component {
      constructor(props) {
        super(props);
        this.state = {};
        this.arr_status = [
          " ",
          " ",
          " ",
          " ",
          " ",
          " ",
        ];
      }
      /********** ********** ********** ********** **********/
      /** function */
      /********** ********** ********** ********** **********/
      componentDidMount() {
        this.loadPageData();
      }
      loadPageData = async () => {
        const { PageStore, APIStore } = this.props;
        const { orderId } = PageStore.getQueryParams();
        const resolve = await APIStore._get(`/v2/api/orders/detail/${orderId}`);
        this.setState({ ...resolve });
      };
      requestApprove = async () => {
        const { ModalStore, APIStore } = this.props;
        ModalStore.openModal("Confirm", {
          title: "",
          body: (
            <React.Fragment>
               ?
              <br />
                 .
            </React.Fragment>
          ),
          onOk: async () => {
            const resolve = await APIStore._post(
              `/v2/api/order/confirm?orderId=${this.state.id}`
            );
            if (resolve && resolve.success) {
              this.loadPageData();
            }
          },
        });
      };
      requestCancel = async () => {
        const { APIStore } = this.props;
        const resolve = await APIStore._post(
          `/v2/api/order/cancel?orderId=${this.state.id}`
        );
        if (resolve && resolve.success) {
          this.loadPageData();
        }
      };
      /********** ********** ********** ********** **********/
      /** render */
      /********** ********** ********** ********** **********/
      render() {
        return (
          <React.Fragment>
            <NavigationLayout title={" "} showBackIcon={true} />

            <div className="container nopadding mt-3">
              <div className="card-round-box pt-1 pb-3 text-center">
                <h5 className="mb-2">
                  <span className="text-primary">
                    <img
                      src="/assets/smartfishing/img/svg/icon-dday.svg"
                      alt=""
                      className="vam"
                    />
                    &nbsp; : {this.state.orderNumber}
                  </span>
                </h5>
                <div className="pay-bg">
                  <ol className="pay-step">
                    {this.arr_status.slice(0, 5).map((text, index) => (
                      <React.Fragment>
                        {this.state.status === " " &&
                          text === " " && (
                            <li key={index} className="active">
                              {this.arr_status[5]}
                            </li>
                          )}
                        {(this.state.status !== " " ||
                          text !== " ") && (
                          <li
                            key={index}
                            className={
                              this.state.status == text ? "active" : ""
                            }
                          >
                            {text}
                          </li>
                        )}
                      </React.Fragment>
                    ))}
                  </ol>
                </div>
              </div>
            </div>
            <p className="space mt-2"></p>

            {/**  */}
            <div className="container nopadding">
              <div className="card">
                <h6></h6>
                <hr className="full mt-1 mb-2" />
                <div className="row no-gutters">
                  <div className="col-6">
                    <strong>{this.state.name}</strong>
                    <br />
                    <small className="grey">
                      
                      <br />
                      
                      <br />
                      
                    </small>
                  </div>
                  <div className="col-6 text-right">
                    <br />
                    {this.state.fishingStartTime} ~ <br />
                    {Intl.NumberFormat().format(this.state.amount || 0)}
                    <br />
                    {Intl.NumberFormat().format(
                      this.state.rideList?.length || 0
                    )}
                    
                  </div>
                </div>
              </div>
            </div>
            <p className="space mt-2"></p>

            {/**  */}
            <div className="container nopadding">
              <div className="card">
                <h6> </h6>
                <hr className="full mt-1 mb-2" />
                <div className="row no-gutters">
                  <div className="col-6">
                    <small className="grey">
                      
                      <br />
                      
                    </small>
                  </div>
                  <div className="col-6 text-right">
                    {this.state.reserveName}
                    <br />
                    {this.state.reservePhone}
                  </div>
                </div>
              </div>
            </div>
            <p className="space mt-2"></p>

            {/**  */}
            <div className="container nopadding">
              <div className="card">
                <h6> </h6>
                {this.state.rideList?.map((data, index) => (
                  <React.Fragment key={index}>
                    <hr className="full mt-1 mb-2" />
                    <div className="row no-gutters">
                      <div className="col-6">
                        <small className="grey">
                          
                          <br />
                          
                          <br />
                          
                        </small>
                      </div>
                      <div className="col-6 text-right">
                        {data["name"]}
                        <br />
                        {data["phone"]}
                        <br />
                        {data["birthday"]}
                      </div>
                    </div>
                  </React.Fragment>
                ))}
              </div>
            </div>
            <p className="space mt-2"></p>

            {/**  */}
            <div className="container nopadding">
              <div className="card">
                <h6> </h6>
                <hr className="full mt-1 mb-2" />
                <div className="row no-gutters">
                  <div className="col-6">
                    <small className="grey">
                      
                      <br />
                      
                      <br />  
                      <br />
                        
                    </small>
                  </div>
                  <div className="col-6 text-right">
                    {this.state.orderDate?.substr(0, 10)}
                    <br />
                    {this.state.payMethod}
                    <br />
                    {Intl.NumberFormat().format(this.state.payTotalAmount || 0)}
                    
                    <br />
                    <strong className="red">
                      -
                      {Intl.NumberFormat().format(
                        this.state.discountAmount || 0
                      )}
                      
                    </strong>
                  </div>
                </div>
                <hr className="full mt-1 mb-2" />
                <div className="row no-gutters">
                  <div className="col-6">
                    <small> </small>
                  </div>
                  <div className="col-6 text-right">
                    <strong className="large">
                      {Intl.NumberFormat().format(this.state.amount || 0)}
                    </strong>
                  </div>
                </div>
              </div>
            </div>
            <p className="space mt-2"></p>

              {/**   */}
              <div className="container nopadding">
                  <div className="card">
                      <h6></h6>
                      <hr className="full mt-1 mb-2" />
                      <div  className="row no-gutters">
                          {(this.state.reserveComment)?
                               this.state.reserveComment : ' ' }
                      </div>
                  </div>
              </div>
              <p className="space mt-2"></p>

            {/**   */}
            <div className="container nopadding">
              <div className="card">
                <h6> </h6>
                <hr className="full mt-1 mb-2" />
                {this.state.shipType == 3 && (
                  <ShipType3PositionView
                    data={{ used: this.state.reservePositions, total: [] }}
                    editable={false}
                  />
                )}
                {this.state.shipType == 5 && (
                  <ShipType5PositionView
                    data={{ used: this.state.reservePositions, total: [] }}
                    editable={false}
                  />
                )}
                {this.state.shipType == 9 && (
                  <ShipType9PositionView
                    data={{ used: this.state.reservePositions, total: [] }}
                    editable={false}
                  />
                )}
              </div>
            </div>
            <p className="space mt-2"></p>

            {/**  */}
            {this.state.status === " " && (
              <React.Fragment>
                <div className="container nopadding">
                  <div className="card">
                    <h6> </h6>
                    <hr className="full mt-1 mb-2" />
                    <div className="row no-gutters">
                      <div className="col-6">
                        <small className="grey"> </small>
                      </div>
                      <div className="col-6 text-right">
                        {this.state.cancelDate}
                      </div>
                    </div>
                    <hr className="full mt-1 mb-2" />
                    <div className="row no-gutters">
                      <div className="col-6">
                        <small></small>
                      </div>
                      <div className="col-6 text-right">
                        <strong className="large red">
                          -
                          {Intl.NumberFormat().format(
                            this.state.refundAmount || 0
                          )}
                          
                        </strong>
                      </div>
                    </div>
                  </div>
                </div>
                <p className="space mt-2"></p>
              </React.Fragment>
            )}

            {/**  */}
            <div className="fixed-bottom">
              <div className="row no-gutters">
                {this.state.status === " " && (
                  <div className="col-6">
                    <a
                      onClick={this.requestApprove}
                      className="btn btn-primary btn-lg btn-block"
                      data-toggle="modal"
                      data-target="#confirmModal"
                    >
                      
                    </a>
                  </div>
                )}
                {this.state.status !== " " &&
                  this.state.status !== " " && (
                    <div
                      className={
                        this.state.status === " " ? "col-6" : "col-12"
                      }
                    >
                      <a
                        className="btn btn-secondary btn-lg btn-block"
                        data-toggle="modal"
                        data-target="#cancelModal"
                      >
                        
                      </a>
                    </div>
                  )}
              </div>
            </div>

            {/**   */}
            <div
              className="modal fade"
              id="cancelModal"
              tabIndex="-1"
              aria-labelledby="cancelModalLabel"
              aria-hidden="true"
            >
              <div className="modal-dialog modal-sm modal-dialog-centered">
                <div className="modal-content">
                  <div className="modal-header">
                    <h5
                      className="modal-title text-center"
                      id="cancelModalLabel"
                    >
                       
                    </h5>
                  </div>
                  <div className="modal-body">
                    <h6 className="text-center red">
                      <img
                        src="./assets/img/svg/icon-alert.svg"
                        alt=""
                        className="vam"
                      />
                      &nbsp;    .
                    </h6>
                    <hr />
                    <ul className="list">
                      <li>
                                  
                            .
                      </li>
                    </ul>
                    <hr />
                  </div>
                  <div className="modal-footer-btm">
                    <div className="row no-gutters">
                      <div className="col-6">
                        <a
                          onClick={this.requestCancel}
                          className="btn btn-primary btn-lg btn-block"
                          data-dismiss="modal"
                        >
                          
                        </a>
                      </div>
                      <div className="col-6">
                        <a
                          className="btn btn-third btn-lg btn-block"
                          data-dismiss="modal"
                        >
                          
                        </a>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </React.Fragment>
        );
      }
    }
  )
);
