import React from "react";
import { inject, observer } from "mobx-react";
import Components from "../../../components";
const {
  LAYOUT: { NavigationLayout },
} = Components;

export default inject(
  "PageStore",
  "APIStore"
)(
  observer(
    class extends React.Component {
      constructor(props) {
        super(props);
        this.check = React.createRef(null);
      }
      /********** ********** ********** ********** **********/
      /** function */
      /********** ********** ********** ********** **********/
      requestSignout = async () => {
        const { APIStore, PageStore } = this.props;
        if (this.check.current?.checked) {
          const resolve = await APIStore._delete(
            "/v2/api/profileManage/delete"
          );
          if (resolve >= 0) {
              PageStore.setAccessToken(null, "cust");
            PageStore.push(`/main/home`);
          }
        }
      };
      /********** ********** ********** ********** **********/
      /** render */
      /********** ********** ********** ********** **********/
      render() {
        return (
          <React.Fragment>
            {/** Navigation */}
            <NavigationLayout title={""} showBackIcon={true} />

            <div className="container nopadding mt-4">
              <h5 className="text-center"> </h5>
              <div className="card-round-box-grey mt-3 pt-3 pb-3">
                <ul className="list">
                  <li>
                     ,  ,   , , 
                            
                     .
                  </li>
                  <li>
                             
                          .
                  </li>
                  <li>
                          .
                  </li>
                </ul>
              </div>
              <label className="control radio mt-2">
                <input
                  ref={this.check}
                  type="checkbox"
                  className="add-contrast"
                  data-role="collar"
                />
                <span className="control-indicator"></span>
                <span className="control-text">
                  <strong>     .</strong>
                </span>
              </label>
              <br />
              <a
                onClick={this.requestSignout}
                className="btn btn-danger btn-lg btn-block"
                data-toggle="modal"
                data-target="#authModal"
              >
                
              </a>
            </div>
          </React.Fragment>
        );
      }
    }
  )
);
